import { Box, Typography } from "@mui/material"

const ProductInfo = () => {
	return (
		<Box sx={(theme) => ({
			height: 600, pt: 10, position: "relative", background: theme.palette.primary.light,
			"@media(max-width:640px)": { height: 400, pt: 5 }
		})}>

			<Box sx={{
				zIndex: 1, mt: 10, width: "43%", height: "100%", position: "absolute", left: 0,
				"@media(max-width:640px)": {
					width: "100%",
					mt: 0,
					position: "relative",
				}
			}}>
				<Box
					sx={{
						height: 600,
						"&>img": { height: "100%", width: "100%", display: "block", zIndex: 1, position: "relative" },
						"@media(max-width:1024px)": {
							height: 330
						},

						"@media(max-width:640px)": {
							display: "none",
						},
					}}>
					<img src="https://picsum.photos/id/252/1000/650?grayscale" alt="no-contact" />
				</Box>
				<Box
					data-scroll-direction="vertical"
					data-scroll
					data-scroll-repeat
					data-scroll-speed="10"
					sx={(theme) => ({
						display: "flex", flexDirection: "column", justifyContent: "center", py: 5, px: 10,
						background: theme.palette.primary.dark, top: "25%", zIndex: 2, position: "absolute", left: "70%", width: 500, height: 370,
						"@media(max-width:1024px)": {
							width: 400,
							height: 300,
							pl: 10,
							left: "40%"
						},
						"@media(max-width:640px)": {
							width: "100%",
							position: "relative",
							left: 0,
							boxSizing: "border-box",
							pl: 0,
							pr: 0,
							top: 0,
							px: 2,
						},
					})}>
					<Typography variant="caption" sx={{ letterSpacing: 5 }}>MAKEUP ARTIST AND HAIR STYLIST</Typography>
					<Typography variant="h2" sx={{ mt: 0.5 }}>HI I'M VOLEX</Typography>
					<Typography variant="body2" sx={{ mt: 2 }}>
						Lorem ipsum dolor, sit amet consectetur adipisicing elit.oluptate nostrum, repellendus atque expedita tempore repudiandae modi consequatur accusantium, officiis ipsam animi vel?
					</Typography>
				</Box>
			</Box>
			<Box
				sx={{
					zIndex: 0, width: "50%", height: 600, position: "absolute", right: 0,
					"@media(max-width:640px)": { display: "none" },
					"&>img": { height: "100%", width: "100%", display: "block" }
				}}>
				<img src="https://picsum.photos/1000/650?grayscale" alt="no-contact" />
			</Box>


		</Box >
	)
}

export default ProductInfo;