import { Box, Typography } from "@mui/material"

const ProductInfo3 = () => {
	return (
		<Box
			data-scroll
			data-scroll-repeat
			data-scroll-speed="5"
			data-scroll-class="fadeIn"

			sx={(theme) => ({
				display: "flex",
				zIndex: 3,
				"&.fadeIn": {
					opacity: 1,
					transition: "opacity 4s",
				},
				background: theme.palette.primary.light,
				opacity: 0,
				"@media(max-width:768px)": {
					flexDirection: "column",
					py: 5,
				},
				"@media(max-width:640px)": {
					py: 10,
				}
			})}>
			<Box sx={{
				display: "flex", pl: 5, flexDirection: "column", justifyContent: "center", ml: "auto", minWidth: 450,
				"@media(max-width:768px)": {
					ml: "0",
					pl: 0,
				},
				"@media(max-width:640px)": {
					width: "100%",
					minWidth: "100%",
					pl: 0,
				}
			}}>
				<Typography variant="h2" sx={{ "@media(max-width:768px)": { textAlign: "center" } }} >Key Success</Typography>
				<Box component="ul" sx={{ mt: 3, pl: 0, ml: 0, "&>li": { px: 2, borderBottom: "1px solid #000", fontSize: 30, pb: 1.5, mb: 2, listStyle: "none" } }}>
					<Typography component="li" >
						NO.1 Customer satisfies
					</Typography>
					<Typography component="li">
						NO.2 10+M Users.
					</Typography>
					<Typography component="li">
						NO.3 Customize Pricing.
					</Typography>
					<Typography component="li">
						NO.4 Marketing strategies.
					</Typography>
				</Box>
			</Box>
			<Box sx={{
				mb: -8, width: "50%", height: 700, "@media(max-width:1024px)": { height: 500 },
				"@media(max-width:768px)": { width: "100%" },
				"&>img": { height: "100%", width: "100%", display: "block" }
			}}>
				<img src="https://picsum.photos/id/212/1000/800" alt="no-contact" />

			</Box>
		</Box>
	)
}

export default ProductInfo3;