import { Box, Container, Typography } from "@mui/material"

const Footer = () => {
	return (
		<Box sx={(theme) => ({ py: 3, background: theme.palette.secondary.main })}>
			<Container maxWidth="lg">
				<Typography sx={{ textAlign: "center" }}>2023, Design by Volex Theme</Typography>
			</Container>
		</Box>
	)
}

export default Footer;