import { Box, Container, Button, Grid, TextField, Typography } from "@mui/material"

const Contact = () => {
	return (
		<Box sx={{
			pt: 15, opacity: 0, pb: 15, "&.fadeIn": {
				opacity: 1,
				transition: "opacity 4s",
			},
			"@media(max-width:640px)": {
				pt: 10,
				pb: 5,
			}
		}} data-scroll
			data-scroll-repeat
			data-scroll-speed="5"

			data-scroll-class="fadeIn"
		>
			<Container maxWidth="lg">
				<Grid container spacing={3}>
					<Grid item xs={12} md={6}>
						<Box sx={{
							width: "100%", display: "flex", justifyContent: "center", height: 600, "&>img": { width: "100%", maxHeight: "100%", display: "block" },
							"@media(max-width:768px)": { display: "none" }
						}}>
							<img src="https://picsum.photos/id/214/500/600" alt="no-contact" />
						</Box>
					</Grid>
					<Grid item xs={12} md={6}>
						<Box sx={(theme) => ({ mt: 5, position: "relative", height: "calc(100% - 42px)", border: `1px solid ${theme.palette.secondary.dark}` })}>
							<Typography variant="h2" sx={{ fontSize: 60, lineHeight: 1, position: "absolute", top: -42, left: 60, maxWidth: 240, fontWeight: 600 }}>Contact volex</Typography>
							<Box sx={{ py: 4, px: 8, mt: 10 }}>
								<Typography sx={{ mb: 5 }}>

									Fill out the form or send an email directly to hello@rougebeauty.com
								</Typography>
								<TextField fullWidth size="small" sx={{ mb: 3 }} placeholder="Username" />
								<TextField fullWidth size="small" sx={{ mb: 3 }} placeholder="Email" />
								<TextField fullWidth size="small" sx={{ mb: 5 }} placeholder="Enter Message" />
								<Button variant="contained" color="primary" size="large" fullWidth>Submit</Button>
							</Box>

						</Box>
					</Grid>
				</Grid>
			</Container>
		</Box>
	)
}

export default Contact;