import React from "react";
import { Box, Typography } from "@mui/material";

const InfoSec = () => {
	return (
		<Box
			sx={{
				zIndex: 4,
				backgroundAttachment: "fixed", position: "relative", backgroundImage: `url("https://picsum.photos/id/217/1000/450")`, backgroundRepeat: "no-repeat", backgroundSize: "cover", py: 20,
				"@media(max-width:640px)": {
					py: 10
				}
			}}>
			<Box sx={(theme) => ({ position: "absolute", top: 0, left: 0, width: "100%", zIndex: 1, height: "100%", opacity: 0.8, backgroundColor: theme.palette.primary.dark })} />
			<Box

				data-scroll
				data-scroll-repeat
				data-scroll-speed="5"
				data-scroll-class="fadeIn"
				sx={{
					textAlign: "center", maxWidth: 700, zIndex: 2, position: "relative", margin: "0 auto",
					opacity: 0,
					"&.fadeIn": {
						opacity: 1,
						transition: "opacity 4s",
					},
				}}>
				<Typography variant="h2">
					The Best Color in the Whole World is the One That Looks Good on You
				</Typography>
			</Box>
		</Box >
	)
}

export default InfoSec;