import { Box, Container, Typography } from "@mui/material"

const ProductInfo2 = () => {
	return (
		<Box sx={{
			py: 25, background: "linear-gradient(110deg, rgba(218,145,204,0.3) 0%, rgba(188,198,152,0.7) 100%)",
			"@media(max-width:768px)": {
				py: 20,

			}
		}}>
			<Container maxWidth="lg" sx={{ position: "relative" }}>
				<Box sx={{ display: "flex", "@media(max-width:768px)": { flexDirection: "column" } }}>
					<Box sx={{
						position: "relative", ml: 10, height: 500, width: 450, "&>img": { borderTopRightRadius: 150, borderBottomRightRadius: 150, borderBottomLeftRadius: 150, display: "block", height: "100%", width: "100%" },
						"@media(max-width:768px)": { width: "50%", ml: "auto", mr: "auto" },
						"@media(max-width:640px)": { width: "100%" }
					}}>
						<img src="https://picsum.photos/id/244/500/550" alt="no-contact" />
						<Box sx={{ "@media(max-width:640px)": { display: "none" }, position: "absolute", left: -100, bottom: -50, height: 300, width: 200, "&>img": { borderTopRightRadius: 100, borderTopLeftRadius: 100, borderBottomLeftRadius: 100, display: "block", height: "100%", width: "100%" } }}>
							<img src="https://picsum.photos/id/243/400/550" alt="no-contact" />
						</Box>
					</Box>

					<Box
						data-scroll
						data-scroll-repeat
						data-scroll-class="fadeIn"
						data-scroll-speed="3"
						sx={{
							"&.fadeIn": {
								opacity: 1,
								transition: "opacity 4s",
							},
							opacity: 0, width: "calc(100% - 490px)", ml: 5, display: "flex", justifyContent: "center", flexDirection: "column",
							"@media(max-width:768px)": {
								width: "100%",
								ml: 0,
								mt: 15,
							}
						}}>
						<Typography variant="body2" sx={{ letterSpacing: 5 }}>Unique Branding</Typography>
						<Typography variant="h2">Brand story</Typography>
						<Typography sx={{ mt: 3 }}>
							Lorem ipsum dolor sit amet consectetur, adipisicing elit. Provident omnis ipsam ea. Magni quae natus ipsa incidunt dolor, laboriosam fugit ut porro magnam sed, voluptas unde facilis nobis? Incidunt, quia?
						</Typography>
					</Box>
				</Box>
			</Container>
		</Box>
	)
}

export default ProductInfo2;