import { Box, Grid, Container, Typography, Button } from "@mui/material"

const Hero = () => {
	return (
		<Box
			data-scroll
			data-scroll-repeat
			data-scroll-class="fadeIn"
			data-scroll-speed="1"
			sx={(theme) => ({
				opacity: 0,
				"&.fadeIn": {
					opacity: 1,
					transition: "opacity 2s",
				},
				px: 2.5, background: theme.palette.secondary.main, height: 650,
			})}
		>
			<Container maxWidth="lg">
				<Grid container spacing={5}>
					<Grid item xs={12} md={6}>
						<Box sx={{
							display: "flex", flexDirection: "column", height: "100%", justifyContent: "center", alignItems: "flex-start",
							"@media(max-width:768px)": {
								pt: 10,
								mt: 0,
							}
						}}>
							<Typography variant="caption" sx={{ letterSpacing: 5 }}>DIGITAL MARKETING</Typography>

							<Typography variant="h2">VOLEX</Typography>
							<Typography variant="body1" sx={{ mt: 3 }}>Lorem ipsum dolor sit amet consectetur, adipisicing elit. Soluta blanditiis cupiditate aperiam sed earum ex recusandae, dolor nemo quis nobis quas delectus autem consectetur illo rerum sequi? Provident, iusto mollitia!</Typography>
							<Button size="large" sx={(theme) => ({
								px: 0, mt: 4,
								position: "relative",
								"&::after": {
									content: `""`,
									position: "absolute",
									height: 5,
									bottom: -10,
									left: 0,
									width: 100,
									background: theme.palette.secondary.dark,
								}
							})} >Let's Quick Discussion</Button>

						</Box>

					</Grid>
					<Grid item xs={12} md={6}>
						<Box
							data-scroll
							data-scroll-repeat
							data-scroll-direction="vertical"
							data-scroll-class="fadeIn"
							data-scroll-speed="3"
							sx={(theme) => ({
								opacity: 0,
								mt: 15,
								"&.fadeIn": {
									opacity: 1,
									transition: "opacity 4s",
								}, height: 600, width: "100%", "&>img": { border: `10px solid ${theme.palette.primary.light}`, width: "100%", height: "100%", display: "block" },

								"@media(max-width:1024px)": {
									height: 450,
									mt: 5,
								},
								"@media(max-width:768px)": {
									mt: 5,
									height: 400,
									width: "60%",
									ml: "auto",
									mr: "auto"
								},
								"@media(max-width:640px)": {
									height: 300,
								}
							})}>
							<img src="https://picsum.photos/500/600?grayscale" alt="no-contact" />

						</Box>
					</Grid>
				</Grid>
			</Container>
		</Box>
	)
}

export default Hero;