import { Box, Container, Button, Grid, Typography } from "@mui/material"
import Facebook from "../icons/FacebookIcon"
import Instagram from "../icons/Instagram"
import Linkedin from "../icons/Linkedin"
import Twitter from "../icons/Twitter"
import Youtube from "../icons/YoutubeIcon"

const Blogs = () => {
	return (
		<Box
			data-scroll
			data-scroll-repeat

			data-scroll-speed="5"
			data-scroll-class="fadeIn"
			sx={(theme) => ({
				opacity: 0,
				"&.fadeIn": {
					opacity: 1,
					transition: "opacity 4s",
				}, background: theme.palette.primary.light, pb: 20, pt: 40, "@media(max-width:1024px)": { pb: 10, pt: 20 },
				"@media(max-width:640px)": { pt: 10 }
			})}>
			<Container>
				<Grid container spacing={3}>
					<Grid item xs={12} md={12} lg={8} sx={{ position: "relative" }}>
						<Typography variant="h2" sx={{ position: "absolute", top: -16, left: 50 }}>Brodcast</Typography>
						<Box sx={{
							width: "100%", height: 350, "&>img": { height: "100%", width: "100%", display: "block" },
							"@media(max-width:640px)": { height: 200 }
						}}>
							<img src="https://picsum.photos/1000/450?grayscale" alt="no-contact" />
						</Box>
					</Grid>
					<Grid item xs={12} md={12} lg={4}>
						<Box sx={{ display: "flex", height: "100%", alignItems: "flex-start", justifyContent: "center", flexDirection: "column" }}>
							<Typography variant="h4" >Top brodcast title goes here</Typography>
							<Typography variant="body2" sx={{ mt: 2 }}>Lorem ipsum dolor sit amet consectetur  doloribus facilis pariatur odio dolorem Lorem ipsum dolor sit amet consectetur  doloribus facilis pariatur odio dolorem.</Typography>
							<Button size="large" sx={(theme) => ({
								px: 0, mt: 2,
								position: "relative",
								"&::after": {
									content: `""`,
									position: "absolute",
									height: 5,
									bottom: -10,
									left: 0,
									width: 100,
									background: theme.palette.secondary.dark,
								}
							})} >Learn more</Button>
						</Box>
					</Grid>
					<Grid item xs={12} md={12} lg={5}>
						<Box sx={{ mt: 7 }}>
							<Typography variant="h3" sx={{ lineHeight: 1.4 }} >Exploring the frontiers of Artificial Intelligence</Typography>
							<Typography variant="body2" sx={{ mt: 2 }}>Lorem ipsum dolor sit amet consectetur adipisicing elit. odio dolorem.</Typography>
							<Box>
								<Typography variant="h4" sx={{ mt: 5 }}>You can connect on social media also.</Typography>
								<Box sx={{ mt: 3, display: "flex", alignItems: "center", "&>span": { mr: 1, cursor: "pointer" } }}>
									<Typography component="span"><Twitter /></Typography>
									<Typography component="span"><Facebook /></Typography>
									<Typography component="span"><Youtube /></Typography>
									<Typography component="span"><Linkedin /></Typography>
									<Typography component="span"><Instagram /></Typography>
								</Box>
							</Box>
						</Box>

					</Grid>
					<Grid item xs={12} md={12} lg={7}>
						<Box sx={{ mt: 7, display: "flex", "@media(max-width:640px)": { flexDirection: "column" } }}>
							<Box sx={{ width: 300, height: 200, "&>img": { width: "100%", height: "100%", display: "block" }, "@media(max-width:640px)": { width: "100%" } }}>
								<img src="https://picsum.photos/300/200" alt="no-contact" />
							</Box>
							<Box sx={{ ml: 3, width: "calc(100% - 324px)", "@media(max-width:640px)": { width: "100%", ml: 0 } }}>
								<Typography sx={{ mt: 2 }} variant="h4" >Top brodcast title goes here</Typography>

								<Typography sx={{ mt: 3 }} variant="body1">Lorem ipsum dolor sit amet consectetur adipisicing elit. Aspernatur dolous facilis pariatur odio dolorem.</Typography>
							</Box>
						</Box>
						<Box sx={{ mt: 7, display: "flex", "@media(max-width:640px)": { flexDirection: "column" } }}>
							<Box sx={{ width: 300, height: 200, "&>img": { width: "100%", height: "100%", display: "block" }, "@media(max-width:640px)": { width: "100%" } }}>
								<img src="https://picsum.photos/id/210/300/200" alt="no-contact" />
							</Box>
							<Box sx={{ ml: 3, width: "calc(100% - 324px)", "@media(max-width:640px)": { width: "100%", ml: 0 } }}>
								<Typography sx={{ mt: 2 }} variant="h4" >Top brodcast title goes here</Typography>

								<Typography sx={{ mt: 3 }} variant="body1">Lorem ipsum dolor sit amet consectetur adipisicing elit. Aspernatur dolous facilis pariatur odio dolorem.</Typography>
							</Box>
						</Box>
					</Grid>
				</Grid>
			</Container>
		</Box>
	)
}

export default Blogs;