import { Box, Typography } from "@mui/material"

const Aboutus = () => {
	return (
		<Box sx={(theme) => ({
			px: 2.5, pt: 20, background: theme.palette.primary.light,
			"@media(max-width:1024px)": { pb: 20, pt: 30 },
			"@media(max-width:768px)": { pt: 55 },
			"@media(max-width:640px)": { pt: 35, pb: 20 }
		})}>
			<Typography data-scroll data-scroll-speed="5" variant="h2" sx={{ maxWidth: 450, margin: "0 auto", textAlign: "center" }}>
				Beauty Inspired by Real Life
			</Typography>
			<Typography data-scroll data-scroll-speed="5" variant="body1" sx={{
				maxWidth: 650, margin: "0 auto", textAlign: "center",
				"@media(max-width:640px)": { mt: 3 }
			}}>
				Lorem ipsum dolor sit amet consectetur adipisicing elit. Cum veritatis perferendis id ullam vitae, fugiat velit expedita deserunt quod qui maxime harum tenetur repudiandae nulla voluptas aut, quis ea voluptates?
			</Typography>
		</Box>
	)
}

export default Aboutus;